.sticker-container {
    width: 100%;
    display: flex;
    flex-direction: row;
    padding-left: 2vw;
    padding-top: 2vw;
}

.left-display {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

.right-display {
    display: flex;
    flex-direction: column;
    padding-left: 0.25vw;
}

.text-box {
    width: 30vw;
    padding: 1.5vw;
    height: 100%;
    display: flex;
    flex-direction: column;
}

.abide-fill {
    display: flex;
    justify-content: center;
    align-items: center;
    overflow: hidden;
    width: 22vw;
    height: 60vw;
}

.abide-fill-fifth {
    display: flex;
    justify-content: center;
    align-items: center;
    overflow: hidden;
    width: 28vw;
    height: 40vw;
    padding-left: 0.25vw;
}

.abide-fill-fifth-right {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    overflow: hidden;
    width: 15vw;
    height: 19.75vw;
    padding-left: 0.25vw;
    padding-bottom: 0.35vw;
}

.abide-fill-fifth-window {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    overflow: hidden;
    width: 15vw;
    height: 19.9vw;
    padding-left: 0.25vw;
    /* padding-bottom: 0.4vw; */
}

.abide-right-container {
    display: flex;
    flex-direction: column;
    /* justify-content: space-between; */
}

.abide-fill-fifth-right img {
    flex-shrink: 0;
    min-width: 100%;
    min-height: 100%;
    object-fit: cover;
}

.abide-fill-fifth-window img {
    flex-shrink: 0;
    min-width: 100%;
    min-height: 100%;
    object-fit: cover;
}

.fill img {
    flex-shrink: 0;
    min-width: 100%;
    min-height: 100%;
    object-fit: cover;
}

.fill-fifth img {
    flex-shrink: 0;
    min-width: 100%;
    min-height: 100%;
    object-fit: cover;
}

.smaller-container {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    padding-top: 0.25vw;
}

.abide-title {
    padding-top: 2.5vw;
    font-size: 4vw;
    font-family: 'Lexend Deca', sans-serif;
    font-weight: 500;
    width: 80vw;
    color: #2d3739;
}

.abide-bible {
    font-size: 1vw;
    font-family: 'Tenor Sans', sans-serif;
    width: 27vw;
    padding-top: 2vw;
    color: #394648;
}

.abide-description {
    font-size: 1vw;
    font-family: 'Lexend Deca', sans-serif;
    font-weight: 500;
    width: 27vw;
    padding-top: 2vw;
    color: #5c6a6f;
}

.bottom-one {
    padding-top: 2vw;
}

.shop-text {
    font-family: 'Tenor Sans', sans-serif;
    font-size: 0.6vw;
    transition: font-size 0.25s;
}

.shop-text:hover {
    font-size: 0.7vw;
}

.button-format {
    /* padding-left: 7vw; */
    padding-top: 2vw;
}

.shop-btn-stickerpage {
    border: solid;
    border-width: thin;
    width: 12vw;
    letter-spacing: 0.15vw;
    padding: 0.15vw 1.2vw;
    text-align: center;
    /* padding-left: 1.5vw; */
    /* margin-bottom: 5vw; */
}
